@tailwind components;
.nav{
    margin-top: auto;
    width: 100%;
    border-top-width: 1px;
    --tw-border-opacity: 1;
    border-color: rgb(229 231 235 / var(--tw-border-opacity));
}
.nav-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.nav-container a{
    display: block;
}
.nav-container li{
    position: relative;
    width: 100%;
    padding: 1rem;
    text-align: center;
}
.navbar-link-active{
    --tw-text-opacity: 1 !important;
    color: rgb(41 171 225 / var(--tw-text-opacity)) !important;
}
.navbar-link-active span{
    visibility: visible;
    opacity: 1;
}
.navbar-link-active svg{
    --tw-text-opacity: 1 !important;
    color: rgb(41 171 225 / var(--tw-text-opacity)) !important;
}
.nav-tracer{
    visibility: hidden;
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    height: 3px;
    width: 100%;
    --tw-bg-opacity: 1;
    background-color: rgb(41 171 225 / var(--tw-bg-opacity));
    opacity: 0;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
}
.home-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-right: 1.5rem;
    padding-left: 1rem;
}
.search-header{
    @extend .home-header;
    position: relative;
    border-bottom-width: 1px;
    --tw-border-opacity: 1;
    border-bottom-color: rgb(229 231 235 / var(--tw-border-opacity));
    padding-top: 1rem !important;
    padding-bottom: 1rem !important;
  }
#intro{
    position: relative;
    height: 150px;
    overflow: hidden;
}
.intro-image{
    height: 100%;
    cursor: pointer;
    background-image: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6)),url('../../assets/images/news-bg.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.intro-overlay{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    cursor: pointer;
    padding: 1rem;
    text-align: center;
}
.tab-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.tab-container a{
    position: relative;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    padding: 0.7rem;
}
.tab-container li{
    width: 100%;
}
.tab-item{
    cursor: pointer;
}
.tab-item p{
    margin-left: 0.5rem;
    --tw-text-opacity: 1;
    color: rgb(75 85 99 / var(--tw-text-opacity));
}
.tab-tracer{
    visibility: hidden;
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    height: 3px;
    width: 100%;
    --tw-bg-opacity: 1;
    background-color: rgb(41 171 225 / var(--tw-bg-opacity));
    opacity: 0;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
}
.tab-item-active p{
    --tw-text-opacity: 1 !important;
    color: rgb(51 51 51 / var(--tw-text-opacity)) !important;
}
.tab-item-active span{
    visibility: visible !important;
    opacity: 1 !important;
}
.tab-item-active svg{
    --tw-text-opacity: 1 !important;
    color: rgb(51 51 51 / var(--tw-text-opacity)) !important;
}
#popular{
    position: relative;
    height: 300px;
    overflow-y: scroll;
    padding: 1.5rem;
}
.btn{
    display: inline-block;
    border-radius: 6px;
    padding: 0.6rem;
    font-size: 1rem;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
}
.btn:hover{
    --tw-shadow: 0 0 20px;
    --tw-shadow-colored: 0 0 20px var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
.btn-primary{
    --tw-bg-opacity: 1;
    background-color: rgb(41 171 225 / var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
}
.btn-primary:hover{
    --tw-shadow-color: #29abe1;
    --tw-shadow: var(--tw-shadow-colored);
}
.btn-warning{
    --tw-bg-opacity: 1;
    background-color: rgb(245 158 11 / var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
}
.btn-warning:hover{
    --tw-shadow-color: #f59e0b;
    --tw-shadow: var(--tw-shadow-colored);
}
.btn-sm{
    font-size: 0.9rem !important;
}
.btn-dark{
    --tw-bg-opacity: 1;
    background-color: rgb(51 51 51 / var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
}
.btn-dark:hover{
    --tw-shadow-color: #333;
    --tw-shadow: var(--tw-shadow-colored);
}
.price-card{
    margin-bottom: 1rem;
    display: flex;
    width: 100%;
    cursor: pointer;
    align-items: center;
    justify-content: space-between;
    border-radius: 6px;
    --tw-bg-opacity: 1;
    background-color: rgb(229 231 235 / var(--tw-bg-opacity));
    padding-left: 0.9rem;
    padding-right: 0.9rem;
    padding-top: 1.1rem;
    padding-bottom: 1.1rem;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
}
.price-card:hover{
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    --tw-shadow: 0 0 40px;
    --tw-shadow-colored: 0 0 40px var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
    --tw-shadow-color: #e5e7eb;
    --tw-shadow: var(--tw-shadow-colored);
}
.price-details{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.icon-wrapper{
    display: flex;
    height: 2.5rem;
    width: 2.5rem;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
}
.icon-wrapper:hover{
    --tw-bg-opacity: 1;
    background-color: rgb(229 231 235 / var(--tw-bg-opacity));
}
.search-result{
    position: absolute;
    top: 57px;
    max-height: 9rem;
    min-height: 3rem;
    width: 100%;
    overflow-y: auto;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    --tw-shadow: 0 17px 27px rgba(0,0,0,0.1);
    --tw-shadow-colored: 0 17px 27px var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
#trending-list{
    height: 100%;
    overflow-y: auto;
    background-color: rgb(243 244 246 / 0.7);
    padding: 1rem;
}
.trending-card{
    display: flex;
    cursor: pointer;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    border-width: 1px;
    --tw-border-opacity: 1;
    border-color: rgb(209 213 219 / var(--tw-border-opacity));
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding: 0.6rem;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
}
.trending-card:hover{
    --tw-shadow: 0 0 30px rgba(0,0,0,0.1);
    --tw-shadow-colored: 0 0 30px var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}